<template>
  <div class="centification container">
    <router-link class="back" to="/user/account">{{'\< '+$t('navbar.user.navItem1')}}</router-link>
    <div class="content-title">
      {{ $t('centification.title') }}
    </div>
    <el-form :model="ruleForm" :rules="rules" ref="ruleForm" label-width="180px" class="centification-form" v-if="type == 0">
      <el-form-item :label="$t('centification.nation')" prop="cityname">
        <el-select v-model.trim="ruleForm.cityname" :placeholder="$t('centification.nationPlaceholder')">
          <el-option :label="item.country_name" :value="item.id" v-for="item in countryAndCardType.country" :key="item.id"></el-option>
        </el-select>
      </el-form-item>
      <el-form-item :label="$t('centification.certificate')" prop="cardtype">
        <el-select v-model.trim="ruleForm.cardtype" :placeholder="$t('centification.certificatePlaceholder')">
          <el-option :label="item.name" :value="item.id"  v-for="item in countryAndCardType.cardType" :key="item.id"></el-option>
        </el-select>
      </el-form-item>
      <el-form-item :label="$t('centification.name')" prop="truename">
        <el-input v-model.trim="ruleForm.truename"></el-input>
        <div class="tips-right">{{ $t('centification.nametips') }}</div>
      </el-form-item>
      <el-form-item :label="$t('centification.certificateNumber')" prop="idcard">
        <el-input v-model.trim="ruleForm.idcard"></el-input>
        <div class="tips-right">{{ $t('centification.certificateNumberPlaceholder') }}</div>
      </el-form-item>
      <el-form-item :label="$t('centification.certificatePhoto')" prop="file">
        <el-row>
          <el-col :span="8">
            <div class="grid-content bg-purple-light">
              <el-upload
              class="avatar-uploader"
              action=""
              :on-remove="handleRemove"
              :on-change="(file,fileList)=>handleChange(file,fileList,'identity_front')"
              :show-file-list="false"
              :auto-upload="false"
              :before-upload="beforeAvatarUpload">
              <img v-if="imageUrl.identity_front" :src="imageUrl.identity_front" class="avatar">
              <i v-else class="el-icon-plus avatar-uploader-icon"><div class="tip">{{ $t('centification.certificatePhotoTips1') }}</div></i>
              
              <!-- <div class="delete"><i class="el-icon-delete" @click="deleteImg('identity_front')"></i></div> -->
            </el-upload>
            </div>
            </el-col>
          <el-col :span="8">
            <div class="grid-content bg-purple">
              <el-upload
                class="avatar-uploader"
                :on-remove="handleRemove"
                action=""
                :on-change="(file,fileList)=>handleChange(file,fileList,'identity_back')"
                :show-file-list="false"
                :auto-upload="false"
                :before-upload="beforeAvatarUpload">
                <img v-if="imageUrl.identity_back" :src="imageUrl.identity_back" class="avatar">
                <i v-else class="el-icon-plus avatar-uploader-icon"><div class="tip">{{ $t('centification.certificatePhotoTips2') }}</div></i>
                
                <!-- <div class="delete"><i class="el-icon-delete" @click="deleteImg('identity_back')"></i></div> -->
              </el-upload>
            </div>
            </el-col>
          <el-col :span="8">
            <div class="grid-content bg-purple-light">
              <el-upload
                class="avatar-uploader"
                :on-remove="handleRemove"
                ref="upload"
                action=""
                :on-change="(file,fileList)=>handleChange(file,fileList,'identity_hold')"
                :show-file-list="false"
                :auto-upload="false"
                :before-upload="beforeAvatarUpload">
                <img v-if="imageUrl.identity_hold" :src="imageUrl.identity_hold" class="avatar">
                <i v-else class="el-icon-plus avatar-uploader-icon"><div class="tip">{{ $t('centification.certificatePhotoTips3') }}</div></i>
                
                <!-- <div class="delete"><i class="el-icon-delete" @click="deleteImg('identity_hold')"></i></div> -->
              </el-upload>
            </div>
            </el-col>
        </el-row>
      </el-form-item>
      <el-form-item :label="$t('form.phone')" prop="phone">
        <el-input v-model.trim="ruleForm.phone" disabled></el-input>
      </el-form-item>
      <el-form-item :label="$t('form.phoneVerifyCode')" prop="sms_verify" class="verify">
        <el-input type="text" auto-complete="off" :placeholder="$t('form.verifyCodePlaceholder')" v-model.trim="ruleForm.sms_verify"></el-input><el-button class="get-verify" @click.native="sendRequestSms($event)" :disabled="!validCode">{{ validTip }}</el-button>
      </el-form-item>
      <el-form-item label="">
        <el-button type="primary" class="sure-submit" @click="submitForm('ruleForm')">{{ $t('form.submit') }}</el-button>
      </el-form-item>
    </el-form>
    <div class="tips" v-if="type == 0">
      <p>{{$t('centification.tips')}}</p>
      <p>{{$t('centification.tips1')}}</p>
      <p>{{$t('centification.tips2')}}</p>
      <p>{{$t('centification.tips3')}}</p>
      <p>{{$t('centification.tips4')}}</p>
    </div>
    <div class="centification-audit centification-status" v-if="type == 1">
      <div class="status">
				<div class="clearfix status-title" v-if="check_status == 1">
          <img src="static/images/user/success.png" alt="" />
          <p>{{ $t('centification.centificationStatus1') }}</p>
        </div>
        <div class="clearfix status-title" v-if="check_status == 0">
          <img src="static/images/user/error.png" alt="" />
          <p>{{ $t('centification.centificationStatus2') }}</p>
        </div>
				<div class="clearfix status-title" v-if="check_status == 2">
          <img src="static/images/user/verify.png" alt="" />
          <p>{{ $t('centification.centificationStatus3') }}</p>
        </div>
			</div>
      <p class="title">{{ $t('centification.userInfo') }}</p>
      

      <div class="info">
				<p class="label">{{ $t('centification.userInfo1') }}<span>{{ info.truename }}</span></p>
				<p class="label">{{ $t('centification.userInfo2') }}<span>{{ info.cardtype_name }}</span></p>
				<p class="label">{{ $t('centification.userInfo3') }}<span>{{ info.idcard }}</span></p>
				<p class="label">{{ $t('centification.userInfo4') }}<span>{{ info.create_time }}</span></p>
        <P class="title noMt" v-if="check_status == 0" @click="handleRecentify">{{ $t('centification.reason') }}</P>
      </div>
      <!-- check_status == 0 -->
    </div>  
  </div>
</template>
<script>
  import { identityData, identityCancel, submitMemberIdentify, getCardTypeAndCountry } from '@/api/user'
  import { sendRequestSms } from '@/api/loginModule'

  export default {
    data() {
      const validateCard = (rule, value, callback) => {
        for (let i = 0; i < this.countryAndCardType.cardType.length; i++) {
          // const element = this.countryAndCardType.cardType[i]
        // if(element.name == '身份证' && element.id == this.ruleForm.cardtype) {
        //   if (!Idcard(value)) {
        //     callback(new Error('身份证号不正确！'))
        //   }
        // }
        }
        callback()
      }
      return {
        imageUrl: {
          identity_front: '',
          identity_back: '',
          identity_hold: ''
        },
        validCode: true,
        validTip: this.$t('verifyCode.title'),
        countryAndCardType: {
          country: [],
          cardType: []
        },
        ruleForm: {
          cityname: '',
          truename: '',
          cardtype: '',
          idcard: '',
          phone: '',
          sms_verify: '',
          identity_front: '',
          identity_back: '',
          identity_hold: ''
        },
        link_userInfo: null,
        rules: {
          truename: [
            { required: true, message: this.$t('centification.message1'), trigger: 'blur' }
          ],
          cardtype: [
            { required: true, message: this.$t('centification.message2'), trigger: 'change' }
          ],
          idcard: [
            { required: true, message: this.$t('centification.message3'), trigger: 'blur' },
            { validator: validateCard, trigger: 'blur' }
          ],
          sms_verify: [
            { required: true, message: this.$t('centification.message4'), trigger: 'blur' }
          ],
          cityname: [
            { required: true, message: this.$t('centification.message5'), trigger: 'change' }
          ]
        },
        type: null,
        check_status: null,
        info: {}
      }
    },
    created() {
      this.getPhoneNum()
      this.isCentification()
    },
    methods: {
      submitForm(formName) {
        this.$refs[formName].validate((valid) => {
          if (valid) {
            console.log(this.ruleForm)
            if (this.ruleForm.identity_front === '' || this.ruleForm.identity_back === '' || this.ruleForm.identity_hold === '') {
              this.$message.error(this.$t('centification.message6'))
              return false
            }
            submitMemberIdentify(this.ruleForm).then(res => {
              console.log('res', res)
              if (res.status !== 1000) {
                this.$message.error(res.msg)
                return false
              }
              this.isCentification()
            }).catch(() => {
            })
          } else {
            console.log('error submit!!')
            return false
          }
        })
      },
      isCentification() {
        identityData().then(res => {
          console.log('实名验证信息---', res)
          if (res.status !== 1000) {
            this.$message.error(res.msg)
            return false
          }
          this.type = res.data.type
          this.check_status = res.data.info.check_status
          this.info = res.data.info
        }).catch(() => {
        })
      },
      handleRecentify() {
        this.$alert(this.info.check_msg || '--', this.$t('centification.reasonTitle'), {
          confirmButtonText: this.$t('centification.recentify'),
          center: true
        }).then(() => {
          identityCancel().then(res => {
            console.log('信息---', res)
            if (res.status !== 1000) {
              this.$message.error(res.msg)
              return false
            }
            this.isCentification()
          }).catch(() => {
          })
        }).catch(() => {
        })
      },
      beforeAvatarUpload(file) {
        // const isJPG = file.type === 'image/jpeg';
        const isLt2M = file.size / 1024 / 1024 < 2
        if (!isLt2M) {
          this.$message.error(this.$t('centification.message7'))
        }
        return isLt2M
      },
      handleRemove(file, fileList) {
        console.log(file, fileList)
      },
      handleChange(file, fileList, str) {
        var reader = new FileReader()
        reader.readAsDataURL(file.raw)
        console.log('str', str)
        const _this = this
        reader.onload = function() {
          _this.ruleForm[str] = reader.result
        }
        this.imageUrl[str] = URL.createObjectURL(file.raw)
      },
      setTime() {
        let time = 60
        if (this.validCode) {
          this.validCode = false
          const _this = this
          const t = setInterval(function() {
            time--
            _this.validTip = time + _this.$t('verifyCode.second')
            if (time === 0) {
              clearInterval(t)
              _this.validTip = _this.$t('verifyCode.getAgain')
              _this.validCode = true
            }
          }, 1000)
          return t
        }
      },
      sendRequestSms(event) {
        const data = {}
        this.setTime()
        data.user_account = this.link_userInfo.phone
        data.request_type = 'identity'
        sendRequestSms(data).then(res => {
          console.log('res', res)
          if (res.status !== 1000) {
            this.$message.error(res.msg)
            return false
          }
        }).catch(() => {
        })
      },
      getPhoneNum() {
        this.link_userInfo = window.localStorage.getItem('link_userInfo')
        this.link_userInfo = JSON.parse(this.link_userInfo)
        console.log(this.link_userInfo)
        if (this.link_userInfo.phone && this.link_userInfo.phone !== '0') {
          this.ruleForm.phone = this.link_userInfo.phone
          this.getCountryAndCardTypeList()
        } else {
          this.$confirm(this.$t('errorTips.bindPhoneFist'), this.$t('dialog.title'), {
            confirmButtonText: this.$t('dialog.sure'),
            cancelButtonText: this.$t('dialog.cancle'),
            type: 'warning'
          }).then(() => {
            this.$router.push('account')
          }).catch(() => {
          })
        }
      },
      getCountryAndCardTypeList() {
        getCardTypeAndCountry().then(res => {
          console.log('res', res)
          if (res.status !== 1000) {
            this.$message.error(res.msg)
            return false
          }
          this.countryAndCardType.country = res.data.country
          this.countryAndCardType.cardType = res.data.cardtype
        }).catch(() => {
        })
      }
    }
  }
</script>
<style lang="less" scoped>
  .centification{
    background: white;
    padding: 20px 25px;
    font-size: 14px;
    margin-bottom: 50px;
    min-height: 770px;
    .back{
      display: inline-block;
      color: #1fa8f6;
      margin-bottom: 20px;
    }
    .content-title{
      padding-top: 9px;
      padding-bottom: 18px;
      border-bottom: 1px solid #e6eaf0;
      font-size: 16px;
    }
    .centification-form{
      padding: 30px 0 10px 90px;
      .tips-right{
        display: inline-block;
        color: #9397a0;
        margin-left: 16px;
        font-size: 12px;
      }
    }
    .delete{
      position: absolute;
      z-index: 2;
      font-size: 24px;
      right: 0px;
      top: 0px;
      background: rgba(0,0,0,.6);
      color: white;
      width: 100%;
      height: 100%;
      display: none;
      transition: all 0.3;
    }
    .tip{
      color: #bababa;
      position: absolute;
      // z-index: 2;
      font-size: 13px;
      left: 0;
      right: 0;
      bottom: -30px;
    }
    .tips{
      font-size: 12px;
      color: #bababa;
      margin-left: 270px;
    }
    
  /*---实名认证状态页样式---*/
  // .status {
  //   margin: 55px  0 0 90px;
  //   font-size: 16px;
  // }
  .status .status-title img{
    margin-right: 10px;
    width: 35px;
    vertical-align: middle;
  }
  .status .status-title span{
    vertical-align: middle;
  }
  .status .success{
    color: #1fa8f6;
  }
  .status .error{
    color: #fc4e5a;
  }
  .status .audit{
    color: #2fd872;
  }
  // .centification-status .info

  // 新款
    .centification-audit{
      .status-title{
        margin-top: 80px;
        text-align: center;
        img{
          width: 52px;
          height: 52px;
        }
        p{
          font-size: 20px;
          color: #3e4558;
          line-height: 35px;
        }
      }
      .title{
        position: relative;
        z-index: 2;
        margin: 0 auto;
        margin-top: 40px;
        margin-bottom: -25px;
        width: 171px;
        height: 41px;
        line-height: 41px;
        background-color: #289afa;
        color: #fff;
        font-size: 16px;
        border-radius: 5px;
        text-align: center;
        &.noMt{
          margin-top: 0;
          cursor: pointer;
        }
      }
      .info{
        box-sizing: border-box;
        width: 420px;
        height: 254px;
        padding: 80px 100px;
        padding-bottom: 0;
        margin: 0 auto;
        border-radius: 30px;
        background-image: url('/static/images/centification-form.png');
        // height: 2px;
        // position: absolute;
        // background: -webkit-repeating-linear-gradient( 135deg, #f19ec2 0, #f19ec2 25%, transparent 0, transparent 50%, #aecbf2 0, #aecbf2 75%, transparent 0, transparent 100% );
        // background: repeating-linear-gradient( -45deg, #f19ec2 0, #f19ec2 25%, transparent 0, transparent 50%, #aecbf2 0, #aecbf2 75%, transparent 0, transparent 100% );
        // background-size: 80px;
         .label{
          color: #9397a0;
          line-height: 24px;
          font-size: 13px;
          span{
            color: #3e4558;
          }
        }
      }
    }
  }
</style>
<style lang="less">
  .centification{
    .avatar-uploader{
      width: 160px;
      display: inline-block;
    }
    .el-input__inner,
    .el-input{
      width: 264px;
    }
    .el-row{
      width: 452px;
    }
    .avatar-uploader .el-upload {
      border: 1px dashed #d9d9d9;
      border-radius: 6px;
      cursor: pointer;
      position: relative;
      overflow: hidden;
    }
    .avatar-uploader .el-upload:hover {
      border-color: #409EFF;
    }
    .verify .el-input__inner,
    .verify .el-input {
        width: 149px;
    }
    .get-verify {
      width: 95px;
      margin-left: 10px;
      display: inline-block;
      padding: 12px 13px;
      background: rgba(31,168,246,.15);
      color: rgba(31, 168, 246, 0.7);
    }
    .sure-submit {
        padding: 12px 112px;
    }
    .avatar-uploader-icon {
      font-size: 28px;
      color: #8c939d;
      width: 135px;
      height: 100px;
      line-height: 100px;
      text-align: center;
    }
    .avatar {
      width: 135px;
      height: 100px;
      display: block;
    }
    .avatar-uploader .el-upload:hover .delete{
      display: block;
    }
    .re-centify{
      padding: 9px 0;
    }
  }

</style>


